body {
  font-family: Roboto;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.image {
  width: 400px;
  height: auto;
}

.cat-description {
  flex-direction: column;
  display: flex;
}

.cat-description > h1 {
  margin-top: 0;
}

.cat-description > h2 {
  margin-bottom: 10px;
}

